// components/SubmissionCard.tsx
'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'

type Props = {
  id: string
  imageUrl?: string
  description: string
  createdAt: string
  reverse?: boolean
}

export default function SubmissionCard({
  id,
  imageUrl,
  description,
  createdAt,
  reverse = false,
}: Props) {
  const router = useRouter()

  const [isEditing, setIsEditing] = useState(false)
  const [text, setText] = useState(description)
  const [saving, setSaving] = useState(false)
  const [deleting, setDeleting] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const onSave = async () => {
    setError(null)
    const trimmed = text.trim()
    if (!trimmed) {
      setError('Description cannot be empty.')
      return
    }
    if (trimmed.length > 2000) {
      setError('Max 2000 characters.')
      return
    }

    setSaving(true)
    try {
      const res = await fetch(`/api/submissions/${id}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ description: trimmed }),
      })
      const bodyText = await res.text()
      if (!res.ok) {
        let msg = 'Update failed'
        try { msg = JSON.parse(bodyText).error || msg } catch {}
        throw new Error(msg)
      }
      setIsEditing(false)
      router.refresh()
    } catch (e: any) {
      setError(e?.message || 'Update failed')
    } finally {
      setSaving(false)
    }
  }

  const onDelete = async () => {
    const ok = confirm('Delete this submission? This cannot be undone.')
    if (!ok) return

    setError(null)
    setDeleting(true)
    try {
      const res = await fetch(`/api/submissions/${id}`, { method: 'DELETE' })
      const bodyText = await res.text()
      if (!res.ok) {
        let msg = 'Delete failed'
        try { msg = JSON.parse(bodyText).error || msg } catch {}
        throw new Error(msg)
      }
      router.refresh()
    } catch (e: any) {
      setError(e?.message || 'Delete failed')
    } finally {
      setDeleting(false)
    }
  }

  const imageColOrder = reverse ? 'md:order-2' : 'md:order-1'
  const textColOrder  = reverse ? 'md:order-1' : 'md:order-2'

  return (
    <article className="border rounded-xl p-4 grid gap-4 md:grid-cols-[260px_1fr] items-start">
      {/* Left/Right: Image (ordered via md:order-*) */}
      <div className={imageColOrder}>
        {imageUrl ? (
          <img
            src={imageUrl}
            alt="submission"
            className="w-full h-auto rounded-lg object-cover max-h-[420px]"
          />
        ) : (
          <div className="w-full aspect-video rounded-lg border grid place-items-center text-sm text-gray-500">
            No image
          </div>
        )}
      </div>

      {/* Opposite side: Text / Meta / Actions */}
      <div className={`grid gap-3 ${textColOrder}`}>
        {isEditing ? (
          <div className="grid gap-2">
            <textarea
              className="border rounded-xl p-3 min-h-28"
              value={text}
              onChange={(e) => setText(e.target.value)}
              maxLength={2000}
            />
            <div className="text-sm opacity-70">{text.length}/2000</div>
            <div className="flex gap-2">
              <button
                onClick={onSave}
                disabled={saving}
                className="border rounded-xl px-3 py-1"
              >
                {saving ? 'Saving…' : 'Save'}
              </button>
              <button
                onClick={() => { setIsEditing(false); setText(description) }}
                disabled={saving}
                className="border rounded-xl px-3 py-1"
              >
                Cancel
              </button>
            </div>
          </div>
        ) : (
          <p className="whitespace-pre-wrap">{description}</p>
        )}

        <time className="text-sm opacity-70">
          {new Date(createdAt).toLocaleString()}
        </time>

        {error ? <p className="text-red-600 text-sm">{error}</p> : null}

        <div className="flex gap-2">
          <button
            onClick={() => setIsEditing(true)}
            disabled={deleting || saving}
            className="border rounded-xl px-3 py-1"
          >
            Edit
          </button>
          <button
            onClick={onDelete}
            disabled={deleting || saving}
            className="border rounded-xl px-3 py-1"
          >
            {deleting ? 'Deleting…' : 'Delete'}
          </button>
        </div>
      </div>
    </article>
  )
}
